.main-slider {
    padding-top: 100px;
    padding-bottom: 100px;
    position: relative;
}
.main-slider__bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}
.main-slider__title {
    font-family: $baseFontFamily;
    font-size: rem(48px);
    margin-bottom: 40px;
}

.main-slider__subtitle {
    font-family: $dopFontFamily;
    font-size: rem(30px);
    line-height: rem(30px);
    color: $ogenix-gray;
    margin-bottom: 24px;
}

.main-slider__btn-box {
    display: flex;
    flex-direction: column;
    align-items: baseline;
}

.main-slider__btn {
    margin: 5px 0;
}


.main-slider__shape_1 {
    display: none;
}
.main-slider__shape_2 {
    display: none;
}
.main-slider__shape_5 {
    display: none;
}
.main-slider__shape_6 {
    display: none;
}
.main-slider__shape_7 {
    display: none;
}
.main-slider__shape_8 {
    display: none;
}
.main-slider__img {
    display: none;
}




/*----Light theme---*/

html.light {
    .main-slider {
        background-color: $ogenix-extra;
    }
    .main-slider__title {
        color: $ogenix-black;
    }
    .main-slider__bg {
        background-image: url("../../../i/main-slider-bg-shape.png");
        opacity: 0.8;
    }
}

/*----Darck theme---*/

html.dark {
    .main-slider {
        background-color: #223d17;
    }
    .main-slider__title {
        color: $white;
    }
    .main-slider__bg {
        background-image: url("../../../i/main-slider-bg-shape-light.png");
        opacity: 0.03;
    }
}

