@font-face {
  font-family: "icomoon";
  font-weight: normal;
  font-style: normal;
  src: url("#{$fontsPath}icomoon.eot");
  src: url(".#{$fontsPath}icomoon.eot?#iefix") format("embedded-opentype"),
    url("#{$fontsPath}icomoon.woff") format("woff"),
    url("#{$fontsPath}icomoon.ttf") format("truetype"),
    url("#{$fontsPath}#icomoon") format("svg");
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


