.footer__row {
    display: flex;
    flex-wrap: wrap;
}
.footer__col {
    flex-basis: 50%;
    padding-right: 50px;
}
.footer__row-bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.footer__copyright {
    margin-bottom: 0;
}
.footer__info-links {
    display: flex;
    li {
        margin-bottom: 0;
        padding-right: 10px;
        &::after {
            content: "/";
            color: #91a589;
            margin-left: 10px;
        }
        &:last-child {
            padding-right: 0;
        }
        &:last-child::after {
            content: none;
        }
        a {
            display: inline;
        }
    }
}