.banner {
    padding-top: 45px;
    padding-bottom: 45px;
    &__item {
        margin-bottom: 30px;
    }
    &__box {
        height: 100%;
        padding: 41px 20px 50px;
        border-radius: 6px;
        overflow: hidden;
        position: relative;
    }
    &__box-1 {
        background-color: $ogenix-black;
    }
    &__box-2 {
        background-color: $ogenix-base;
    }
    &__img {
        display: none;
    }
    &__shape-1 {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: -168px;
        z-index: 1;
    }
    &__shape-2 {
        position: absolute;
        left: 227px;
        transform: translateY(-50%);
        top: 50%;
        z-index: 1;
    }
    &__shape-3 {
        position: absolute;
        left: 200px;
        top: -14px;
        z-index: 1;
    }
    &__shape-5 {
        position: absolute;
        bottom: -46px;
        right: -29px;
        z-index: 1;
    }
    &__shape-6 {
        position: absolute;
        bottom: 10px;
        right: 134px;
        z-index: 1;
    }
    &__subtitle {
        @extend %subtitle;
        color: $white;
        line-height: rem(34px);
        position: relative;
        z-index: 5;
    }
    &__title {
        @extend %title;
        color: $white;
        font-size: rem(30px);
        position: relative;
        z-index: 5;
        max-width: 250px;
        margin-bottom: 10px;
    }
    &__btn {
        z-index: 5;
        margin-bottom: 10px;
    }
}