.subscribe {
    padding: 45px 0;
    position: relative;
    z-index: 2;
    &__box {
        position: relative;
    }
    &__shape-1 {
       display: none;
    }
    &__shape-2 {
       display: none;
    }
    &__shape-4 {
       display: none;
    }
    &__shape-5 {
        display: none;
    }
    &__content {
        position: relative;
        overflow: hidden;
        background-color: $ogenix-base;
        padding: 60px 20px 48px;
        border-radius: 50px;
        border-top-left-radius: 0;
    }
    &__shape-3 {
        position: absolute;
        right: -30px;
        top: 0;
        opacity: .40;
        z-index: 1;
    }
    &__row {
        display: flex;
        flex-direction: column;
    }
    &__col_left {
        display: flex;
        align-items: center;
        margin-bottom: 30px;
    }
    &__icon-box {
        font-size: rem(64px);
        color: $white;
    }
    &__title-box {
        margin-left: 20px;
    }
    &__subtitle {
        font-family: $dopFontFamily;
        font-size: rem(20px);
        font-weight: 700;
        line-height: rem(30px);
        color: #d1f7c1;
    }
    &__title {
        font-size: rem(24px);
        font-weight: 900;
        line-height: rem(32px);
        color: $white;
        margin-top: 0;
        margin-bottom: 0;
    }
    &__shape-6 {
        display: none;
    }
    &__form {
        width: 100%;
    }
    &__input-box {
        position: relative;
        input[type="email"] {
            height: 57px;
            width: 100%;
            border: none;
            outline: none;
            padding-right: 75px;
            font-size: 14px;
            color: $ogenix-gray;
            font-weight: 500;
            background-color: $white;
            border-radius: 6px;
            padding-left: 30px;
        }
    }
    &__btn {
        position: absolute;
        top: 5px;
        bottom: 5px;
        right: 5px;
        width: 54px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 6px;
        background: $ogenix-primary;
        border: 0;
        transition: all 500ms ease;
        padding: 0;
        font-size: 1rem;
        color: $white;
        &:hover {
            background: $ogenix-base;
        }
    }
  

}