//fonts
$google-font: "https://fonts.googleapis.com/css2?family=Caveat:wght@700&family=Manrope:wght@500;600;800&display=swap";
$prefix: prefix- !default;
$baseFontSize: 16px;
$baseFontFamily: 'Manrope', sans-serif;
$dopFontFamily: 'Caveat', cursive;
$baseFontWeight: 500;
$baseLineHeight: 30px;
$bdr-radius: 6px;

$mobilePadding: (12px / $baseFontSize) * 1rem;
$desktopPadding: 1rem;

// Paths

$imagesPath: "../i/";
$fontsPath: "../fonts/";
$usersFilesPath: "../userfiles/";

// Colors
$black: #000000;
$white: #ffffff;
$ogenix-base: #50a72c;;
$ogenix-black: #2a491d;
$ogenix-primary: #ffbd3c;
$ogenix-gray: #7d8978;
$ogenix-extra: #f3f3ed;
$ogenix-bdr-color: #eaeae2;
$ogenix-olive-color: #91a589;



$colors: (
  "black": $black,
  "white": $white,
  "ogenix-base": $ogenix-base,
  "ogenix-black": $ogenix-black,
  "ogenix-primary": $ogenix-primary,
  "ogenix-gray": $ogenix-gray,
  "ogenix-extra": $ogenix-extra,
  "ogenix-bdr-color": $ogenix-bdr-color,
  "ogenix-olive-color": $ogenix-olive-color,
) !default;
