.testimonial {
    padding-top: 45px;
    padding-bottom: 45px;
    background-image: url(../../../i/testimonial-one__bg-img.jpeg);
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    overflow: hidden;
    &__bg-img {
        display: none;
    }
    &__bg-shape {
        position: absolute;
        right: 0;
        bottom: -222px;
    }
    &__subtitle {
        @extend %subtitle;
        color: $ogenix-base;
    }
    &__title {
        @extend %title;
        font-size: rem(35px);
        line-height: rem(45px);
    }
    &__btn {
        margin-top: 40px;
    }
    &__img {
        display: none;
    }
    &__right {
        padding-top: 50px;
        position: relative;
    }
    &__shape-1 {
        position: absolute;
        top: 0;
        left: -80px;
    }
    &__shape-2 {
        display: none;
    }
    &__content {
        padding: 40px 20px 39px;
        text-align: center;
        box-shadow: 0px 15px 50px 0px rgb(0 0 0 / 10%);
        border-radius: $bdr-radius;
        position: relative;
        z-index: 2;
        margin-bottom: 40px;
        &::after {
            content: ''; 
            position: absolute;
            bottom: -85px;
            right: 50px;
            border: 50px solid transparent;
            border-top: 100px solid transparent;
            z-index: -1;
            transform: rotate(40deg);
        }
    }
    &__quot {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 74px;
        height: 74px;
        background-color: $ogenix-primary;
        border-radius: 50%;
        margin: 0 auto;
        span {
            color: $white;
            font-size: rem(32px);
        }
    }
    &__text-review {
        font-size: rem(20px);
        font-weight: 500;
        line-height: rem(36px);
        margin-top: 14px;
        margin-bottom: 26px;
    }
    &__deteils {
        display: flex;
        justify-content: space-around;
        align-items: center;
        p {
            line-height: rem(30px);
            margin-top: 0;
            margin-bottom: 0;
        }
    }
    &__name {
        font-size: rem(18px);
        font-weight: 900;
    }
    &__position {
        font-size: rem(14px);
        font-weight: 500;
        color: $ogenix-base;
    }
    &__photo {
        width: 92px;
        height: 92px;
        margin: 0 auto;
        border-radius: 50%;
        overflow: hidden;
        img {
            width: 100%;
            object-fit: cover;
        }
    }
    &__arrows {
        display: flex;
        justify-content: center;
        margin-top: 30px;
    }
    &__arrow {
        display: inline-block;
        height: 52px;
        width: 52px;
        line-height: 52px;
        border-radius: 50%;
        font-size: 16px;
        margin: 0 5px;
        text-align: center;
        -webkit-transition: all 500ms ease;
        transition: all 500ms ease;
    }

}


/*----Light theme---*/

html.light {
    .testimonial {
        background-image: url(../../../i/testimonial-one__bg-img.jpeg);
    }
    .testimonial__title {
        color: $ogenix-black;
    }
    .testimonial__text, .testimonial__text-review {
        color: $ogenix-gray;
    }
    .testimonial__content {
        background-color: $white;
        &::after {
            border-top: 100px solid $white;
        }
    }
    .testimonial__name {
        color: $black;
    }
    .testimonial__arrow {
        border: 2px solid rgba(42, 73, 29, .20);
        color: rgba(42, 73, 29, .20);
        &:hover {
            border: 2px solid rgba(42, 73, 29, 1);
            color: rgba(42, 73, 29, 1);
            background-color: transparent;
        }
    }

}

/*----Dark theme---*/

html.dark {
    .testimonial {
        background-image: url(../../../i/testimonial-one__bg-img-dark.jpeg);
    }
    .testimonial__title {
        color: $white;
    }
    .testimonial__text, .testimonial__text-review {
        color: $ogenix-olive-color;
    }
    .testimonial__content {
        background-color: $ogenix-black;
        &::after {
            border-top: 100px solid $ogenix-black;;
        }
    }
    .testimonial__name {
        color: $white;
    }
    .testimonial__bg-shape {
        opacity: 0.1;
    }
    .testimonial__arrow {
        border: 2px solid rgba(255, 255, 255, .20);
        color: rgba(255, 255, 255, .20);
        &:hover {
            border: 2px solid rgba(255, 255, 255, 1);
            color: rgba(255, 255, 255, 1);
            background-color: transparent;
        }
    }
}