.search-popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 9999;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    opacity: 0;
    transform: translateY(-120%);
    transition: transform 500ms ease, opacity 500ms ease;
    &__overlay {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: $ogenix-black;
        opacity: 0.75;
        cursor: pointer;
    }
    &__content {
        width: 100%;
        max-width: 560px;
        padding-left: 15px;
        padding-right: 15px;
    }
    &__input-box {
        position: relative;
        input {
            width: 100%;
            background-color: $white;
            font-size: rem(16px);
            border: none;
            outline: none;
            height: 66px;
            padding-left: 30px;
            border-radius: $bdr-radius;
        }
    }
    &__btn {
        padding: 0;
        width: 66px;
        height: 66px;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        position: absolute;
        top: 0;
        right: -1px;
        border-radius: 0;
        background-color: $ogenix-base;
        border: 0;
        border-top-right-radius: $bdr-radius;
        border-bottom-right-radius: $bdr-radius;
        span {
            height: auto;
            width: auto;
            background-color: transparent;
            border-radius: 50%;
            color: $white;
            font-size: 22px;
            line-height: inherit;
            text-align: center;
            top: 0;
            margin-right: 0;
            padding-left: 0;
            transition: all 500ms ease;
        }
    }
}

.search-popup.active {
    opacity: 1;
    transform: translateY(0%);
}
