.header {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
    &__row {
        display: flex;
        align-items: stretch;
    }
    &__logo {
        background-color: $ogenix-black;
        padding: 10px 60px;
    }
    &__right {
        flex-grow: 1;
        display: flex;
        flex-direction: column;
    }
    &__right-top {
        display: none;
    }
    &__right-bottom {
        flex-grow: 1;
        padding-left: 15px;
        padding-right: 15px;
    }
    &__menu {
        height: 100%;
    }
    &__button {
        width: 40px;
        span {
            display: block;
            width: 100%;
            height: 6px;
            background-color: $ogenix-base;
            border-radius: 6px;
            margin-bottom: 8px;
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}

.menu {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    &__theme {
        display: none;
    }
    &__nav {
        display: none;
    }
    &__buttons {
        display: none;
    }
}




/*----Light theme---*/

html.light {
    .header {
        background-color: $white;
    }
}

/*----Darck theme---*/

html.dark {
    .header {
        background-color: #1a3111;
    }
}