.products__menu {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
} 
.products__text {
    width: 100%;
    margin-bottom: 0;
    text-align: left;
}
.products__buttons {
    margin-bottom: 0;
    margin-right: 20px;
}
.products__sort {
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    margin-bottom: 0;
    margin-left: 20px;
}

.products__all._grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
}

.products__all._line {
    .product {
        flex-direction: row;
    }
    .product__img-box {
        flex: 0 0 270px;
    }
    .product__content {
        align-items: flex-start;
        padding-top: 20px;
    }
}

