html {
  @include getCssVar(font-size);
}

body {
  @include getCssVar(font-family);
  font-size: 1rem;
}

p {
   font-weight: $baseFontWeight;
   line-height: $baseLineHeight;
}

.wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1;
  overflow-x: hidden;
  margin-top: 85px;
}

.container {
  width: 100%;
  margin: auto;
  padding: 0 15px;
} 

%title {
  font-family: $baseFontFamily;
  font-size: rem(35px);
  line-height: rem(45px);
  font-weight: 800px;
  margin-top: 0;
  margin-bottom: 0;
}

%subtitle {
  display: block;
  font-family: $dopFontFamily;
  font-size: rem(24px);
  line-height: rem(24px);
  margin-bottom: 10px;
}

p {

}

.btn {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  text-align: center;
  text-decoration: none;
  color: $white;
  font-size: rem(16px);
  line-height: 2rem;
  font-weight: 800;
  border-radius: 25px;
  padding: 9px 30px 11px;
  overflow: hidden;
  transition: all 0.5s linear;
  z-index: 1;
  &:before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    width: 60px;
    border-top-right-radius: 25px;
    border-bottom-right-radius: 25px;
    clip-path: polygon(50% 0, 100% 0%, 100% 100%, 0% 100%);
    transition: all 0.5s linear;
    z-index: -1;
  }
  &:hover {
    &:before {
      width: 200%;
    }
  }
}
.btn_yellow {
  background-color: $ogenix-primary;
  &:before {
    background-color:  #f7b534;
  }
}
.btn_green {
  background-color: $ogenix-base;
  &:before {
    background-color:  #4a9e27;
  }
}
.scroll-to-up {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  background: $ogenix-base;
  position: fixed;
  bottom: 40px;
  right: 35px;
  z-index: 99;
  transition: background-color .3s, opacity .5s, visibility .5s;
  opacity: 0;
  visibility: hidden;
  &:hover {
    cursor: pointer;
    background: $ogenix-black;
  }
  span {
    color: $white;
    font-size: 18px;
    line-height: 50px;
    -webkit-transition: all 0.4s ease;
    transition: all 0.4s ease;
    position: relative;
    display: inline-block;
  }
}
.scroll-to-up.show {
  opacity: 1;
  visibility: visible;
}

/*----Darck theme---*/

html.dark {
  body {
    background-color: #1a3111;
  }
}



