.cart {
    padding-top: 45px;
    padding-bottom: 45px;
    &__responsive {
        position: relative;
        display: block;
        width: 100%;
        overflow-x: auto;
        -webkit-overflow-scrolling: touch;
    }
    &__sum {
        padding-top: 45px;
    }
    &__total-list {
        max-width: 375px;
        list-style-type: none;
        padding-left: 0;
        li {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 10px;
        }
    }
    &__subtitle {
        display: block;
        color: $ogenix-black;
        font-size: rem(20px);
        margin-right: 75px;
        font-weight: 800;
        width: 150px;
        margin-top: 0;
        margin-bottom: 0;
    }
    &__price {
        display: block;
        color: $ogenix-gray;
        font-size: rem(18px);
        font-weight: 500;
        margin-top: 0;
        margin-bottom: 0;
    }
    &__total-list li:last-child &__price {
        color: $ogenix-base;
        font-weight: 700;
    }
    &__buttons {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }
    &__button {
        margin-bottom: 10px;
    }
    
}

.table {
    width: 100%;
    thead {
        vertical-align: bottom;
        tr {
            border-color: inherit;
            border-style: solid;
            border-width: 0;
            th {
                color: $ogenix-black;
                font-size: rem(20px);
                line-height: rem(30px);
                font-weight: 800;
                padding: 0;
                border: none;
                padding-bottom: rem(21px);
                &:first-child {
                    text-align: left;
                }
                &:last-child {
                    text-align: right;
                }
            }
        }
    }
    tbody {
        vertical-align: inherit;
        tr {
            position: relative;
            border-color: inherit;
            border-style: solid;
            border-width: 0;
            td {
                font-size: rem(18px);
                font-weight: 500;
                color: $ogenix-gray;
                vertical-align: middle;
                border-top: 1px solid $ogenix-bdr-color;
                padding-top: 30px;
                padding-bottom: 30px;
                padding-left: 10px;
                padding-right: 10px;
                letter-spacing: 0;
                text-align: center;
                &:first-child {
                    text-align: left;
                    padding-left: 0;
                }
            }
            &:last-child {
                td {
                    border-bottom: 1px solid $ogenix-bdr-color;
                }
            }
        }
    }
    &__product {
        display: flex;
        align-items: center;
    }
    &__img {
        width: 120px;
        min-width: 120px;
        height: 120px;
        border-radius: $ogenix-bdr-color;
        overflow: hidden;
        margin-right: 35px;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    &__subtitle {
        color: $ogenix-black;
        font-size: rem(20px);
        font-weight: 800;
        margin: 0;
    }
    &__quantity {
        position: relative;
        width: 98px;
        border-radius: 0px;
        height: 50px;
        margin: 0 auto;
        button {
            width: 24px;
            height: 24px;
            color: $ogenix-gray;
            font-size: rem(8px);
            position: absolute;
            top: 1px;
            right: 1px;
            background-color: $ogenix-extra;
            border: none;
            border-left: 1px solid $ogenix-bdr-color;
            display: flex;
            align-items: center;
            justify-content: center;
            outline: none;
        }
        button.minus {
            bottom: 1px;
            top: auto;
            border-top: 1px solid $ogenix-bdr-color;
            border-top-right-radius: 0px;
            border-bottom-right-radius: 0px;
        }
        input {
            width: 98px;
            border-radius: $ogenix-bdr-color;
            height: 50px;
            border: 0;
            -webkit-appearance: textfield;
            -moz-appearance: textfield;
            appearance: textfield;
            font-family: $baseFontFamily;
            padding-left: 30px;
            outline: none;
            font-size: rem(14px);
            font-weight: 500;
            color: $ogenix-gray;
            background-color: $ogenix-extra;
        }
    }
    &__remove {
        display: flex;
        justify-content: end;
    }
}

.coupon {
    &__input {
        width: 100%;
        max-width: 375px;
        border-radius: $bdr-radius;
        background-color: $ogenix-extra;
        padding-left: 30px;
        padding-right: 30px;
        margin-right: 20px;
        font-family: $baseFontFamily;
        font-size: rem(14px);
        color: $ogenix-gray;
        height: 50px;
        margin-bottom: 20px;
        border: none;
        outline: none;
        font-weight: 500;
    }
    &__btn {
        margin: auto;
    }
}

/*----Darck theme---*/

html.dark {
    .table thead tr th, .table__subtitle, .cart__subtitle {
        color: $ogenix-gray;
    }
 }