.footer {
    &__top {
        position: relative;
        background-color: $ogenix-black;
        margin-top: -92px;
        z-index: 1;
        overflow: hidden;
        padding-top: 90px;
        padding-bottom: 45px;
    }
    &__bg {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        top: -155px;
        background-image: url(../../../i/site-footer-bg-img.png);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        mix-blend-mode: luminosity;
        opacity: 0.03;
        z-index: -1;
    }
    &__ripped-paper {
        position: absolute;
        top: -1px;
        left: 0;
        right: 0;
        height: 60px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        z-index: 1;
    }
    &__text {
        font-size: rem(20px);
        font-weight: 700;
        line-height: rem(32px);
        color: $white;
        margin-bottom: 30px;
    }
    &__icons {
        padding-left: 0;
        li {
            margin-left: 5px;
            margin-right: 5px;
            &:first-child {
                margin-left: 0;
            }
            a {
                width: 40px;
                height: 40px;
                display: flex;
                justify-content: center;
                align-items: center;
                background-color: #223d17;
                font-size: rem(14px);
                color: $white;
                border-radius: 50%;
                overflow: hidden;
                transition: all 500ms ease;
                &:hover {
                    background-color: $white;
                    color:  #223d17;
                }
            }
        }
    }
    &__col {
        margin-bottom: 30px;
    }
    
    &__subtitle {
        display: inline-block;
        font-size: rem(20px);
        color: $white;
        line-height: rem(30px);
        font-weight: 900;
        position: relative;
        &::after {
            content: "";
            position: absolute;
            top: 15px;
            right: -31px;
            width: 21px;
            height: 3px;
            background: linear-gradient(to right, rgb(80, 167, 44) 0%, rgb(255, 189, 60) 100%);
            background: -moz-linear-gradient(to right, rgb(80, 167, 44) 0%, rgb(255, 189, 60) 100%);
            background: -webkit-linear-gradient(to right, rgb(80, 167, 44) 0%, rgb(255, 189, 60) 100%);
            background: -ms-linear-gradient(to right, rgb(80, 167, 44) 0%, rgb(255, 189, 60) 100%);
        }
    }
    &__nav {
        list-style-type: none;
        padding-left: 0;
        margin-top: 0;
        li {
            margin-bottom: 7px;
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
    &__link {
        text-decoration: none;
        font-size: rem(15px);
        font-weight: 500;
        color: #91a589;
        -webkit-transition: all 500ms ease;
        transition: all 500ms ease;
    }
    &__addres {
        font-size: rem(15px);
        font-weight: 500;
        color: #91a589;
        margin-top: 0;
    }
    &__tel {
        color: $white;
        font-size: rem(20px);
        text-decoration: none;
        display: inline-block;
        padding-bottom: 10px;
        border-bottom: 1px solid #91a589;
        transition: all 500ms ease;
        margin-bottom: 20px;
    }
    &__mail {
        text-decoration: none;
        display: block;
        font-size: rem(20px);
        font-weight: 500;
        color: #91a589;
    }
    &__gallery {
        display: flex;
        flex-wrap: wrap;
        gap: 10px;
    }
    &__gallery-img {
        width: 80px;
        height: 80px;
        img {
            width: 100%;
            object-fit: cover;
        }
    }
    &__bottom {
        padding: 32px 0 36px;
        background-color: #223d17;
        z-index: 1;
        p, a {
            text-decoration: none;
            margin-top: 0;
            text-align: center;
            font-size: rem(15px);
            font-weight: 500;
            margin: 0;
            color: #91a589;
        }
    }
    &__copyright {
        margin-bottom: 10px;
    }
    &__info-links {
        list-style-type: none;
        padding-left: 0;
        margin-top: 0;
        margin-bottom: 0;
        li {
            margin-bottom: 10px;
        }
        a {
            display: block;
        }
    }
 
}

/*----Light theme---*/

html.light {
    .footer__ripped-paper {
        background-image: url(../../../i/site-footer-ripped-paper.png);
    }
}

/*----Darck theme---*/

html.dark {
    .footer__ripped-paper {
        background-image: url(../../../i/site-footer-ripped-paper-dark.png);
    }
}