.header__right-top {
    display: block;
    background-color: #2a491d;
}


.top-menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 15px;
    padding-right: 15px;
    &__right {
        display: flex;
        align-items: center;
    }
    &__hello {
        font-size: rem(13px);
        color: #91a589;
        margin-top: 0;
        margin-bottom: 0;
    }
    &__icons {
        background-color: #223d17;
        padding: 10px 40px 10px;
        margin-left: 30px;
    }
    &__left {
        display: flex;
    }
    &__contact {
        display: block;
        text-decoration: none;
        font-family: $baseFontFamily;
        font-size: rem(14px);
        color: #91a589;
        -webkit-transition: all 500ms ease;
        transition: all 500ms ease;
        margin-right: 20px;
        span {
            color: $ogenix-primary;
            margin-right: 10px;
        }
        &:hover {
            color: $white;
        }
    }
}

.header__button {
    display: none;
}

.menu {
    &__nav {
        display: flex;
        order: -3;
        margin-right: 20px;
        list-style-type: none;
        margin-top: 0;
        margin-bottom: 0;
        li {
            padding-top: 30px;
            padding-bottom: 30px;
            margin-left: 20px;
            margin-right: 20px;
            position: relative;
            &::before {
                content: "";
                height: 3px;
                width: 100%;
                background-image: linear-gradient(to right, rgb(80, 167, 44) 0%, rgb(255, 189, 60) 100%);
                background-image: -moz-linear-gradient(to right, rgb(80, 167, 44) 0%, rgb(255, 189, 60) 100%);
                background-image: -webkit-linear-gradient(to right, rgb(80, 167, 44) 0%, rgb(255, 189, 60) 100%);
                background-image: -ms-linear-gradient(to right, rgb(80, 167, 44) 0%, rgb(255, 189, 60) 100%);
                position: absolute;
                bottom: 0;
                left: 0;
                transition: transform 500ms ease;
                transform: scale(0, 1);
            }
            // &:hover::before{
            //     transform: scale(1, 1);
            // }
            a {
                text-decoration: none;
                font-size: 16px;
                display: flex;
                align-items: center;
                color: $ogenix-gray;
                position: relative;
                -webkit-transition: all 500ms ease;
                transition: all 500ms ease;
                font-weight: 600;
            }
        }
    }
    &__has-submenu {
        position: relative;
        // &:hover {
        //     ul {
        //         opacity: 1;
        //         visibility: visible;
        //         transform: scaleY(1) translateZ(0px);
        //     }
        // }
        ul {
            position: absolute;
            z-index: 99;
            top: 100%;
            left: 0;
            min-width: 200px;
            background-color: white;
            list-style-type: none;
            padding: 20px;
            border-radius: 6px;
            opacity: 0;
            visibility: 0;
            -webkit-transform-origin: top center;
            transform-origin: top center;
            -webkit-transform: scaleY(0) translateZ(100px);
            transform: scaleY(0) translateZ(100px);
            -webkit-transition: opacity 500ms ease, visibility 500ms ease, -webkit-transform 700ms ease;
            transition: opacity 500ms ease, visibility 500ms ease, -webkit-transform 700ms ease;
            transition: opacity 500ms ease, visibility 500ms ease, transform 700ms ease;
            transition: opacity 500ms ease, visibility 500ms ease, transform 700ms ease, -webkit-transform 700ms ease;
            li {
                padding-top: 10px;
                padding-bottom: 10px;
                padding-left: 20px;
                padding-right: 20px;
                margin-left: 0;
                margin-right: 0;
                border-radius: 6px;
                &:hover {
                    background-color: $ogenix-bdr-color;
                }
                &::before {
                    content: none;
                }
            }
        }
    }   
    &__is-submenu {
        position: reletive;
        padding-right: 15px;
        &::after {
            content: "";
            width: 5px;
            height: 5px;
            border: solid $ogenix-gray;
            border-width: 0 2px 2px 0;
            position: absolute;
            right: 0;
            top: 50%;
            transform: rotate(45deg) translateY(-50%);
            -webkit-transform: rotate(45deg) translateY(-50%);
            -webkit-transition: -webkit-transform 500ms ease;
            transition: -webkit-transform 500ms ease;
            transition: transform 500ms ease;
            transition: transform 500ms ease, -webkit-transform 500ms ease;
        }
    }
    // &__has-submenu:hover &__is-submenu::after {
    //     transform: rotate(-135deg) translateY(-50%);
    //     -webkit-transform: rotate(-135deg) translateY(-50%);
    // }
}

.menu__has-submenu.active {
    &::before{
        transform: scale(1, 1);
    }
    ul {
        opacity: 1;
        visibility: visible;
        transform: scaleY(1) translateZ(0px);
    }
}

.menu__has-submenu.active .menu__is-submenu::after {
    transform: rotate(-135deg) translateY(-50%);
    -webkit-transform: rotate(-135deg) translateY(-50%);
}

/*----Darck theme---*/

html.dark {
    .menu__has-submenu ul {
      background-color: #1a3111;
    }
  }
