.menu__theme {
    display: block;
    margin-right: 20px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-transition: all 500ms ease;
    transition: all 500ms ease;
}

.menu__theme-button {
    width: 1.2rem;
    height: 1.2rem;
}

/*----Light theme---*/

html.light {
    .menu__theme {
        background-color: $ogenix-base;
        &:hover {
            background-color: #2a491d;
        }
    }
    .menu__theme-button {
        background: url(../../../i/moon.svg) 0 0 / 100% no-repeat;
    }
}

/*----Darck theme---*/

html.dark {
    .menu__theme {
        background-color: #2a491d;
        &:hover {
            background-color: $ogenix-base;
        }
    }
    .menu__theme-button {
        background: url(../../../i/sun.svg) 0 0 / 100% no-repeat;
    }
}

