/*----change theme button----*/

.page__theme {
    position: fixed;
    z-index: 10;
    right: -2.8rem;
    top: 50%;
    border-bottom-left-radius: 7px;
    border-top-left-radius: 7px;
    &:hover {
        right: 0; 
    }
}

/*----Light theme---*/

html.light {
    .page__theme {
        &:before {
            content: "";
            width: 1rem;
            height: 1rem;
            border: solid $ogenix-black;
            border-width: 3px 0 0 3px;
            transform: translate(0, -50%) rotate(-45deg);
            position: absolute;
            left: 7px;
            top: 50%;
        }
        &:hover {
            &:before {
                content: none
            }
        }
        
    }
    .page__theme-button {
        //background: url(../../../i/moon.svg) 0 0 / 100% no-repeat;
        background: asset(image, 'moon.svg') 00 / 100% no-repeat;
    }

}

/*----Dark theme---*/

html.dark {
    .page__theme {
        &:before {
            content: "";
            width: 1rem;
            height: 1rem;
            border: solid $ogenix-bdr-color;
            border-width: 2px 0 0 2px;
            transform: translate(0, -50%) rotate(-45deg);
            position: absolute;
            left: 5px;
            top: 50%;
        }
        &:hover {
            &:before {
                content: none
            }
        }
    }
    .page__theme-button {
        background: asset(image, 'sun.svg') 00 / 100% no-repeat;
    }
}