.news {
    padding-top: 90px;
    padding-bottom: 45px;
    &__title-box {
        max-width: 450px;
        text-align: center;
        margin: auto;
        margin-bottom: 50px;
    }
    &__title {
        @extend %title;
    }
    &__subtitle {
        @extend %subtitle;
        color: $ogenix-base;
    }
    &__grid {
        display: grid;
        gap: 30px;
    }
    &__item {
        max-width: 100%;
        display: flex;
        flex-direction: column;
    }
    &__item:hover &__img::after {
        opacity: 0.6;
    }
    &__img {
        width: 100%;
        position: relative;
        overflow: hidden;
        border-radius: $bdr-radius;
        z-index: 1;
        &::after {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            transition-delay: .1s;
            transition-timing-function: ease-in-out;
            transition-duration: .7s;
            transition-property: all;
            background: $ogenix-black;
            opacity: 0;
            z-index: 1;
            content: "";
            border-radius: $bdr-radius;
        }
        img {
            width: 100%;
            object-fit: cover;
        }
    }
    &__content {
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        position: relative;
        box-shadow: 0px 10px 60px 0px rgb(0 0 0 / 10%);
        padding: 21px 20px 12px;
        text-align: center;
        border-radius: $bdr-radius;
        margin-top: -100px;
        margin-left: 15px;
        margin-right: 15px;
        border-top-right-radius: 0;
        z-index: 2;
    }
    &__meta {
        list-style-type: none;
        padding-left: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        li {
            margin-right: 10px;
            &::after {
                content: "|";
                margin-left: 10px;
            }
            &:last-child {
                margin-right: 0;
            }
            &:last-child::after {
                content: none;
            }
            a {
                text-decoration: none;
                font-size: rem(14px);
                font-weight: 500;
                line-height: rem(24px);
                -webkit-transition: all 500ms ease;
                transition: all 500ms ease;
                span {
                    color: $ogenix-base;
                    margin-right: 6px;
                }
            }
        }

    }
    &__article-title {
        flex: 1;
        font-size: rem(20px);
        font-weight: 900;
        line-height: rem(30px);
        margin-top: 6px;
        margin-bottom: 24px;
        a {
            text-decoration: none;
            margin-top: 0;
            margin-bottom: 0;
            -webkit-transition: all 500ms ease;
            transition: all 500ms ease;
        }
    }
    &__bottom {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-top: 10px;
        border-top: 1px solid #eaeae2;
        a {
            text-decoration: none;
            margin-top: 0;
            margin-bottom: 0;
            font-size: rem(14px);
            font-weight: 500;
            line-height: rem(24px);
            -webkit-transition: all 500ms ease;
            transition: all 500ms ease;
        }
    }
    &__read-more {
        display: block;
    }
    &__comments {
        span {
            color: $ogenix-primary;
            margin-right: 6px;
        }
    }
    &__data {
        position: absolute;
        top: -33px;
        right: 0;
        p {
            margin-top: 0;
            margin-bottom: 0;
            font-size: rem(12px);
            font-weight: 700;
            line-height: rem(12px);
            color: $white;
            display: inline-block;
            background-color: $ogenix-base;
            padding: 11px 14px 10px;
            letter-spacing: 0.1em;
            border-top-left-radius: $bdr-radius;
            border-top-right-radius: $bdr-radius;
        }
    }
}


/*----Light theme---*/

html.light {
    .news__content {
        background-color: $white;
    }
    .news_title, .news__article-title a {
        color: $ogenix-black;
    }
    .news__meta li {
        &::after {
            color: $ogenix-gray;
        }
        a {
            color: $ogenix-gray;
        }
    }
    .news__bottom a {
        color: $ogenix-gray;
    }
}

/*----Darck theme---*/

html.dark {
    .news__content {
        background-color: #1a3111;;
    }
    .news__title, .news__article-title a {
        color: $white;
    }
    .news__meta li {
        &::after {
            color: #91a589;
        }
        a {
            color: #91a589;
        }
    } 
    .news__bottom {
        border-top: 1px solid #91a589;
        a {
            color: #91a589;
        }
    }
}