.carousel-dots {
    display: flex;
    justify-content: center;
    margin-top: 30px;
    ul {
        list-style-type: none;
        padding-left: 0;
        display: flex;
        li {
            width: 15px;
            height: 15px;
            border-radius: 50%;
            margin-right: 5px;
            button {
                color: transparent;

            }
        }
    }
} 

/*----Light theme---*/

html.light {
    .carousel-dots li {
        border: 2px solid $ogenix-bdr-color;
        background-color: $ogenix-bdr-color;
    }
    .carousel-dots li.slick-active {
        background-color: white;
        border: 2px solid $ogenix-base;
    }
}


/*----Darck theme---*/

html.dark {
    .carousel-dots li {
        border: 2px solid $ogenix-black;
        background-color: $ogenix-black;
    }
    .carousel-dots li.slick-active {
        background-color: #1a3111;
        border: 2px solid $ogenix-base;
    }
}