

.gallery {
    padding: 45px 0;
    &__item {
        padding-top: 20px;
        padding-bottom: 20px;
    }
    &__content {
        position: relative;
        border-radius: 6px;
        -webkit-transition: all 500ms ease;
        transition: all 500ms ease;
    }
    &__img {
        position: relative;
        &::before {
            content: "";
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            border-radius: 6px;
            background-color: $black;
            opacity: 0.3;
            visibility: hidden;
            -webkit-transform-origin: bottom center;
            transform-origin: bottom center;
            -webkit-transform: scaleY(0) translateZ(100px);
            transform: scaleY(0) translateZ(100px);
            -webkit-transition: opacity 500ms ease, visibility 500ms ease, -webkit-transform 700ms ease;
            transition: opacity 500ms ease, visibility 500ms ease, -webkit-transform 700ms ease;
            transition: opacity 500ms ease, visibility 500ms ease, transform 700ms ease;
            transition: opacity 500ms ease, visibility 500ms ease, transform 700ms ease, -webkit-transform 700ms ease;
        }

        img {
            width: 100%;
            object-fit: cover;
        }
    }
    &__text-box {
        position: absolute;
        bottom: -20px;
        left: 50%;
        width: 80%;
        height: 30%;
        border-radius: 6px;
        background-color: $white;
        background-image: url(./../../../i/gallery-one-shape-1.png);
        background-repeat: no-repeat;
        background-position: right;
        background-size: contain;
        padding-top: 30px;
        padding-left: 30px;
        z-index: 2;
        transform: translateX(-50%) translateY(-66%);
        transition: opacity 500ms ease, visibility 500ms ease, transform 500ms ease;
        visibility: hidden;
        opacity: 0;
    }
    &__title {
        font-family: $baseFontFamily;
        font-size: rem(16px);
        font-weight: 900;
        line-height: rem(16px);
        color: $ogenix-black;
        position: relative;
        margin-top: 0;
        margin-bottom: 0;
        &::before {
            content: "";
            width: 30px;
            height: 2px;
            background-image: linear-gradient(to right, rgb(80, 167, 44) 0%, rgb(255, 189, 60) 100%);
            background-image: -moz-linear-gradient(to right, rgb(80, 167, 44) 0%, rgb(255, 189, 60) 100%);
            background-image: -webkit-linear-gradient(to right, rgb(80, 167, 44) 0%, rgb(255, 189, 60) 100%);
            background-image: -ms-linear-gradient(to right, rgb(80, 167, 44) 0%, rgb(255, 189, 60) 100%);
            position: absolute;
            left: 0;
            top: -10px;
        }
    }
    &__text {
        font-size: rem(12px);
        font-weight: 500;
        line-height: rem(14px);
        color: $ogenix-gray;
        margin-top: 4px;
    }
    &__link {
        position: absolute;
        top: -27px;
        right: 20px;
        z-index: 2;
        a {
            text-decoration: none;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 52px;
            height: 52px;
            border-radius: 50%;
            background-color: $ogenix-base;
            font-size: rem(16px);
            color: $white;
            -webkit-transition: all 500ms ease;
            transition: all 500ms ease;
            margin-top: 0;
            margin-bottom: 0;
        }
    }
    &__content:hover &__text-box {
        visibility: visible;
        transform: translateX(-50%) translateY(0%);
        opacity: 1;
    }
    &__content:hover &__img::before {
        visibility: visible;
        -webkit-transform: scaleY(1) translateZ(0px);
        transform: scaleY(1) translateZ(0px);
    }
}


