.menu__buttons {
    display: block;
    order: -1;
    margin-right: 20px;
    padding-right: 20px;
    border-right: 1px solid $ogenix-bdr-color;
    display: flex;
    align-items: center;
}

.menu__search {
    text-decoration: none;
    position: relative;
    display: flex;
    align-items: center;
    font-size: rem(16px);
    font-weight: 300;
    -webkit-transition: all 500ms ease;
    transition: all 500ms ease;
    margin-right: 20px;
    span {
        font-size: rem(17px);
        color: $ogenix-black;
        padding-right: 10px;
    }
}

.menu__cart {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: $ogenix-primary;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: $white;
    font-size: 18px;
    margin-right: 20px;
    &:hover {
        background-color: $ogenix-base;
    }
}

.page__theme {
    order: -2;
}

/*----Darck light---*/

html.light {
    .menu__search {
        color: $ogenix-black;
        span {
            color: $ogenix-black;
        }
    }
  }

/*----Darck theme---*/

html.dark {
    .menu__search {
        color: $white;
        span {
            color: $white;
        }
    }
}