.about-us {
    padding-top: 120px;
    padding-bottom: 120px;
    overflow: hidden;
    &__img-box {
        position: relative;
    }
    &__shape-1 {
        position: absolute;
        left: -73px;
        bottom: 0;
        z-index: -1;
    }
    &__shape-2 {
        position: absolute;
        left: -73px;
        top: 0;
        opacity: .20;
        z-index: -1;
    }
    &__shape-4 {
        position: absolute;
        right: 61px;
        top: 173px;
        z-index: -1;
    }
    &__shape-5 {
        position: absolute;
        right: 29px;
        bottom: 356px;
        z-index: -1;
    }
    &__shape-6 {
        position: absolute;
        right: 16px;
        bottom: 84px;
        z-index: -1;
    }
    &__shape-7 {
        position: absolute;
        left: -141px;
        bottom: 127px;
        z-index: -1;
    }
    &__shape-8 {
        position: absolute;
        left: -141px;
        bottom: 127px;
        z-index: -1;
    }
    &__shape-9 {
        position: absolute;
        right: 27px;
        top: 60px;
        z-index: -1;
    }
    &__shape-10 {
        position: absolute;
        right: -10px;
        top: -14px;
        z-index: -1;
    }
    &__img {
        img {
            width: 100%;
        }
    }
    &__experience {
        background-color: $ogenix-base;
        color: $white;
        position: absolute;
        bottom: 0;
        left: 45px;
        max-width: 334px;
        width: 100%;
        border-radius: 6px;
        display: flex;
        align-items: center;
        padding: 20px 20px 20px;
    }
    &__experience-icon {
        margin-right: 10px;
        span {
            font-size: rem(50px);
        }
    }
    &__experience-text {
        p {
            font-size: rem(18px);
            font-weight: 400;
            line-height: rem(36px);
            display: inline-block;
            margin-top: 0;
            margin-bottom: 0;
        }
        span {
            font-size: rem(40px);
            font-weight: 900;
            line-height: rem(50px);
            margin-right: 10px;
            vertical-align: middle;
        }
    }
    &__content-box {
        padding-top: 60px;
    }
    &__title {
        @extend %title;
        margin-bottom: 40px
    }
    &__subtitle {
        @extend %subtitle;
        color: $ogenix-base;
        margin-bottom: 10px;
    }
    &__list {
        list-style-type: none;
        padding-left: 0;
        li {
            display: inline-block;
            position: relative;
            align-items: center;
            background-color: rgb(255, 255, 255);
            box-shadow: 0px 6px 30px 0px rgb(0 0 0 / 7%);
            padding: 12px 30px 12px 60px;
            border-radius: 6px;
            margin-left: 10px;
            margin-bottom: 10px;
            font-size: rem(18px);
            font-weight: 900;
            line-height: rem(33px);
            position: relative;
            &:before {
                content: "";
                width: 16px;
                height: 16px;
                border-radius: 50%;
                background-color: $ogenix-primary;
                position: absolute;
                top: 50%;
                left: 30px;
                transform: translateY(-50%);
            }
        }
    }
    &__text {
        margin-bottom: 30px;
    }
    &__text-green {
        font-size: rem(18px);
        font-weight: 700;
        color: $ogenix-base;
        margin-top: 30px;
        margin-bottom: 30px;
    }
    &__bottom {
        display: flex;
        align-items: center;
        margin-top: 32px;
        padding-top: 41px;
        border-top: 1px solid #eaeae2;
        margin-bottom: 40px;
    }
    &__bottom-img {
        width: 64px;
        min-width: 64px;
        height: 64px;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    &__bottom-text {
        font-size: rem(24px);
        font-weight: 900;
        line-height: rem(29px);
        margin-left: 28px;
        margin-top: 0;
        margin-bottom: 0;
    }

}

/*----Light theme---*/

html.light {
    .about-us__shape-3 {
        position: absolute;
        left: 57px;
        top: -17px;
        z-index: -1;
    }
    .about-us__title {
        color: $ogenix-black;
    }
    .about-us__list {
        li {
            color: $ogenix-black;
        }
    }
    .about-us__text {
        color: $ogenix-gray;
    }
    .about-us__bottom-text {
        color: $ogenix-black;
    }
}

/*----Dark theme---*/

html.dark {
    .about-us__shape-3  {
        display: none;
    }
    .about-us__title {
        color: $white;
    }
    .about-us__list {
        li {
            background-color: #223d17;
            color: $white;
        }
    }
    .about-us__text {
        color: $white;
    }
    .about-us__bottom-text {
        color: $white;
    }
}
