.google-map {
    padding-top: 45px;
}
.contact-details {
    padding-top: 45px;
    padding-bottom: 45px;
    &__list {
        list-style-type: none;
        padding-left: 0;
    }
    &__item {
        display: flex;
        align-items: center;
        margin-bottom: 10px;
    }
    &__logo {
        margin-right: 15px;
        span {
            font-size: rem(32px);
            color: $ogenix-primary;
        }
    }
    &__text {
        margin-top: 0;
        margin-bottom: 0;
    }
    &__link {
        margin-top: 0;
        margin-bottom: 0;
        text-decoration: none;
        font-size: 16px;
        font-weight: 700;
        line-height: 30px;
    }
}

.feedback {
    padding-bottom: 45px;
    &__container {
        width: 100%;
        margin: auto;
    }
    &__title {
        @extend %title;
        text-align: center;
        margin-bottom: 30px;
    }
    &__subtitle {
        @extend %subtitle;
        color: $ogenix-base;
        text-align: center;
    }
    &__form {
        input {
            height: 50px;
            width: 100%;
            border: none;
            background-color: $ogenix-extra;
            padding-left: 30px;
            padding-right: 30px;
            outline: none;
            font-size: rem(14px);
            color: $ogenix-gray;
            display: block;
            font-weight: 500;
            border-radius: $bdr-radius;
            margin-bottom: 20px;
        }
        textarea {
            font-size: rem(14px);
            color: $ogenix-gray;
            height: 160px;
            width: 100%;
            background-color:$ogenix-extra;
            padding: 15px 30px 30px;
            border: none;
            outline: none;
            margin-bottom: 0px;
            font-weight: 500;
            border-radius: $bdr-radius;
            margin-bottom: 20px;
        }
    }
    &__btn {
        display: block;
        margin: auto;
    }
}

/*----Light theme---*/

html.light {
    .contact-details__text {
        color: $ogenix-gray;
    }
    .contact-details__link {
        color: $ogenix-black;
    }
    .hot-products__title {
        color: $ogenix-black;
    }
}

/*----Darck theme---*/

html.dark {
    .contact-details__text {
        color: #91a589;
    }
    .contact-details__link {
        color: $white;
    }
    .hot-products__title {
        color: $white;
    }
    .feedback__title {
        color: $white;
    }
}