/*----Float in X from -10px to -30px -*/

.float-box-x {
    -webkit-animation-name: float-box-x;
    animation-name: float-box-x;
    -webkit-animation-duration: 2s;
    animation-duration: 2s;
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
    animation-timing-function: linear;
}

@keyframes float-box-x {
    0% {
        transform: translateX(-30px);
    }
    50% {
        transform: translateX(-10px);
    }
    100% {
        transform: translateX(-30px);
    }
}

/*----Float in Y from -10px to -20px -*/

.float-box-y {
    -webkit-animation-name: float-box-y;
    animation-name: float-box-y;
    -webkit-animation-duration: 2s;
    animation-duration: 2s;
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
    animation-timing-function: linear;
}

@keyframes float-box-y {
    0% {
        transform: translateY(-20px);
    }
    50% {
        transform: translateY(-10px);
    }
    100% {
        transform: translateY(-20px);
    }
}

/*----Zoom -*/

.zoominout {
    animation-name: zoomInOut;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
    -webkit-animation-name: zoomInOut;
    -webkit-animation-duration: 3s;
    -webkit-animation-iteration-count: infinite;
    -webkit-animation-timing-function: ease-in-out;
    -moz-animation-name: zoomInOut;
    -moz-animation-duration: 3s;
    -moz-animation-iteration-count: infinite;
    -moz-animation-timing-function: ease-in-out;
    -ms-animation-name: zoomInOut;
    -ms-animation-duration: 3s;
    -ms-animation-iteration-count: infinite;
    -ms-animation-timing-function: ease-in-out;
    -o-animation-name: zoomInOut;
    -o-animation-duration: 3s;
    -o-animation-iteration-count: infinite;
    -o-animation-timing-function: ease-in-out;
}

@keyframes zoomInOut {
    0% {
        -webkit-transform: rotate(0deg) scale(0.7);
        transform: rotate(0deg) scale(0.7);
        opacity: 0;
    }
    50% {
        -webkit-transform: rotate(180deg) scale(1);
        transform: rotate(180deg) scale(1);
        opacity: 1;
    }
    100% {
        -webkit-transform: rotate(360deg) scale(0.7);
        transform: rotate(360deg) scale(0.7);
        opacity: 0;
    }
}

/*----Slide in Left -*/

.slideInLeft {
    -webkit-animation-name: slideInLeft;
    animation-name: slideInLeft;
    animation-duration: 2500ms;
    animation-delay: 100ms;

}

@keyframes slideInLeft {
    0% {
        -webkit-transform: translate3d(-100%, 0, 0);
        transform: translate3d(-100%, 0, 0);
        visibility: visible;  
    }
    100% {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }
}

@keyframes slideMain {
    0% {
        -webkit-transform: translate3d(+100%, 0, 0);
        transform: translate3d(+100%, 0, 0);
        opacity: 0;
    }
    10% {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
        opacity: 1;
    }
    100% {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
        opacity: 1;
    }

}

/*-----Animation elements from js-----*/

.slide-from-up {
    transform: translate(0px, -120%);
    opacity: 0;
    transition: all 1s ease 0s;
}

.slide-from-up.animate-js {
    transform: translate(0px, 0px);
    opacity: 1;
}

.slide-from-down {
    transform: translate(0px, 120%);
    opacity: 0;
    transition: all 1s ease 0s;
}

.slide-from-down.animate-js,  .animate-js .slide-from-down{
    transform: translate(0px, 0px);
    opacity: 1;
}

.slide-from-down_delay-2 {
    transition: all 1s ease 0.2s;
}
.slide-from-down_delay-4 {
    transition: all 1s ease 0.4s;
}
.slide-from-down_delay-6 {
    transition: all 1s ease 0.6s;
}
.slide-from-down_delay-8 {
    transition: all 1s ease 0.8s;
}
.slide-from-down_delay-10 {
    transition: all 1s ease 1s;
}

.slide-from-left {
    transform: translate(-120%, 0px);
    opacity: 0;
    transition: all 1s ease 0s;
}

.slide-from-left.animate-js {
    transform: translate(0px, 0px);
    opacity: 1;
}