.hot-products {
    padding: 45px 0;
    &__title-box {
        margin-bottom: 50px;
        text-align: center;
        padding-left: 20px;
        padding-right: 20px
    }
    &__title {
        @extend %title;
    }
    &__subtitle {
        @extend %subtitle;
        color: $ogenix-base;
    }
    &__carousel {
        margin-left: -15px;
        margin-right: -15px;
        div {
            padding-left: 15px;
            padding-right: 15px;
        }
    } 
}

/*----Light theme---*/

html.light {
    .hot-products__title {
      color: $ogenix-black;
    }
}

/*----Darck theme---*/

html.dark {
    .hot-products__title {
        color: $white;
    }
}
  
