.shop {
  padding-top: 45px;
  padding-bottom: 45px;
  &__sidebar {
    margin-bottom: 30px;
  }
  &__search {
    border-radius: $bdr-radius;
    position: relative;
    background-color: $ogenix-primary;
    input {
        width: 100%;
        height: 70px;
        background-color: $ogenix-primary;
        border-radius: $bdr-radius;
        padding-left: 30px;
        padding-right: 30px;
        font-size: rem(16px);
        color: $white;
        border: none;
        outline: none;
        font-weight: 500; 
        &::placeholder {
            color: $white;
        }
    }
  }
  &__sidebar-field {
    padding: 27px 30px 30px;
    background-color: $ogenix-extra;
    border-radius: $bdr-radius;
    margin-top: 30px;
  }
  &__subtitle {
    color: $ogenix-black;
    font-size: rem(20px);
    font-weight: 800;
    line-height: rem(20px);
    margin-top: 0;
    margin-bottom: 22px;
  }
  &__categories-list {
    list-style: none;
    padding-left: 0;
    margin: 0;
    li {
        margin-bottom: 5px;
        &:last-child {
            margin-bottom: 0px;
        }
    }
  }
  &__categories-item {
    position: relative;
    display: block;
    text-decoration: none;
    line-height: rem(24px);
    font-size: rem(16px);
    text-transform: capitalize;
    color: $ogenix-gray;
    font-weight: 500;
    border-radius: $bdr-radius;
    transition: all 0.3s ease;
    z-index: 1;
    padding: 9px 0px 9px;
    span {
        position: absolute;
        top: 50%;
        right: 10px;
        transform: translateY(-50%);
        font-size: rem(13px);
        color: $ogenix-black;
        font-weight: 500;
        transition: all 200ms linear;
        transition-delay: 0.1s;
    }
    &:hover {
        color: $ogenix-black;
        background: $white;
        padding-left: 34px;
        font-weight: 700;
    }
    &:hover span {
        color: $ogenix-base;
        font-weight: 700;
    }
    &::before {
        position: absolute;
        left: 10px;
        top: 7px;
        font-family: 'icomoon';
        content: "\ea3c";
        opacity: 1;
        font-size: 15px;
        color: $ogenix-primary;
        line-height: 30px;
        transform: scale(0);
        transition: all 200ms linear;
        transition-delay: 0.1s;
        z-index: 1;
    }
    &:hover::before {
        transform: scale(1);
    }
  }
  &__bestseller-list {
    list-style-type: none;
    padding-left: 0;
    margin: 0;
  }
  &__bestseller-item {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  &__bestseller-img {
    width: 70px;
    height: 70px;
    border-radius: $bdr-radius;
    overflow: hidden;
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 0px 40px 0px rgb(0 0 0 / 5%);
    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
  }
  &__bestseller-content {
    margin-left: 15px;
  }
  &__bestseller-title {
    font-size: rem(16px);
    font-weight: 800;
    letter-spacing: 0;
    line-height: rem(16px);
    margin-top: 7px;
    margin-bottom: 7px;
    color: $ogenix-black;
  }
  &__bestseller-price {
    font-size: rem(13px);
    font-weight: 700;
    line-height: rem(15px);
    color: $ogenix-gray;
    margin: 0;
  }

}

.price {
    &__input {
        display: flex;
    }
    &__fields {
        display: flex;
        margin-top: 15px;
    }
    &__field {
        display: block;
        color: $ogenix-gray;
        font-size: rem(14px);
        font-weight: 500;
        line-height: 40px;
    }
    &__field_one {
        margin-right: 5px;
        &::after {
            content: "-";
            color: $ogenix-gray;
            font-size: rem(14px);
            margin-left: 5px;
        } 
    }
    &__slider {
        height: 5px;
        position: relative;
        background-color: $white;
        border-radius: $bdr-radius;
    }
    &__progress {
        height: 100%;
        background-color: $ogenix-primary;
        border-radius: $bdr-radius;
        position: absolute;
        left: 0%;
        right: 50%;

    }
    &__range-input {
        position: relative;
        input {
            position: absolute;
            height: 5px;
            width: 100%;
            top: -5px;
            pointer-events: none;
            background: none;
            appearance: none;
        }
        input[type="range"]::-webkit-slider-thumb {
            height: 14px;
            width: 14px;
            border-radius: 50px;
            background-color: $ogenix-primary;
            appearance: none;
            pointer-events: auto;
        }
        input[type="range"]::-moz-range-thumb {
            height: 14px;
            width: 14px;
            border-radius: 50px;
            background-color: $ogenix-primary;
            appearance: none;
            pointer-events: auto;
        }
    }
}

.products {
    &__menu {
        position: relative;
        display: flex;
        flex-direction: column;
        margin-bottom: 30px;
    }
    &__text {
        font-size: rem(16px);
        font-weight: 500;
        color: $ogenix-gray;
        text-align: center;
        margin-bottom: 30px;
    }
    &__sort {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        //margin-bottom: 30px;
    }
    &__buttons {
        display: flex;
        align-items: center;
        margin-bottom: 30px;
    }
    &__button {
        width: 16px;
        height: 16px;
        display: grid;
        -webkit-transition: all 500ms ease;
        transition: all 500ms ease;
        margin-right: 10px;
        div {
            background-color: $ogenix-gray;
        }
    }
    &__button.active {
        div {
            background-color: $ogenix-black;
        }
    }
    &__button_grid {
        grid-template-columns: 1fr 1fr 1fr;
        gap: 2px;
    }
    &__button_line {
        grid-template-columns: 1fr 2fr;
        gap: 2px;
    }
    &__select {
        width: 100%;
        max-width: 340px;
    }
    &__item {
        margin-bottom: 30px;
    }
    &__all._grid {
        .product__text {
            display: none;
        }
    }
    &__all._line {
        .product__text {
            display: block;
            text-align: left;
        }
    }
    &__pagination {
        display: flex;
        justify-content: center;
        padding-top: 30px;
        padding-bottom: 30px;
    }
}
.pagination {
    &__list {
        display: flex;
        padding-left: 0;
        list-style-type: none;
        margin-top: 0;
        margin-bottom: 0;
    }
    &__item {
        margin-right: 7px;
        &:last-child {
            margin-right: 0;
        }
    }
    &__link {
        height: 47px;
        width: 50px;
        text-align: center;
        line-height: 47px;
        display: inline-block;
        text-decoration: none;
        color: $ogenix-gray;
        background-color: $ogenix-extra;
        border-radius: $bdr-radius;
        font-weight: 700;
        font-size: rem(14px);
        -webkit-transition: all 500ms ease;
        transition: all 500ms ease;
        position: relative;
    }
    &__link.prev {
        &::after {
            content: "";
            width: 8px;
            height: 8px;
            border:  solid $ogenix-gray;
            border-width: 2px 0 0 2px;
            transform: translate(-50%, -50%) rotate(-45deg);
            position: absolute;
            left: 50%;
            top: 50%;
        }
    }
    &__link.next {
        &::after {
            content: "";
            width: 8px;
            height: 8px;
            border:  solid  $ogenix-gray;
            border-width: 0px 2px 2px 0px;
            transform: translate(-50%, -50%) rotate(-45deg);
            position: absolute;
            left: 50%;
            top: 50%;
        }
    }
    
}