.main-slider__img {
    display: block;
    width: 50%;
    max-width: 650px;
    position: absolute;
    top: 150px;
    left: 50%;
}

.main-slider__img-one.animate-js {
    opacity: 0;
    animation-name: slideMain;
    animation-duration: 10s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
    -webkit-animation-name: slideMain;
    -webkit-animation-duration: 10s;
    -webkit-animation-iteration-count: infinite;
    -webkit-animation-timing-function: ease-in-out;
    -moz-animation-name: slideMain;
    -moz-animation-duration: 10s;
    -moz-animation-iteration-count: infinite;
    -moz-animation-timing-function: ease-in-out;
    -ms-animation-name: slideMain;
    -ms-animation-duration: 10s;
    -ms-animation-iteration-count: infinite;
    -ms-animation-timing-function: ease-in-out;
    -o-animation-name: slideMain;
    -o-animation-duration: 10s;
    -o-animation-iteration-count: infinite;
    -o-animation-timing-function: ease-in-out;
}

.main-slider__shape_3 {
    position: absolute;
    top: -50px;
    left: 0;
}

.main-slider__shape_4 {
   position: absolute;
   opacity: 0.1;
   top: 120px;
   left: -120px;
}

.main-slider__shape_8 {
    right: 70px;
 }

