.main-slider {
    padding-top: 200px;
    padding-bottom: 200px;
}

.main-slider__container {
    position: relative;
    z-index: 2;
    padding-left: 30px;
    padding-right: 30px;
}

.main-slider__title {
    max-width: 700px;
    font-size: rem(90px);
    line-height: rem(90px);
    margin-top: 20px;
    position: relative;
    &:before {
        content: "";
        width: 62px;
        height: 63px;
        position: absolute;
        top: -30px;
        right: 130px;
    }
}

.main-slider__btn-box {
    flex-direction: row;
}

.main-slider__btn {
    margin: 0 5px;
}

/*----Light theme---*/

html.light {
    .main-slider__title:before {
        background-image: url("../../../i/main-slider-shape-9.png");
    }
  
}

/*----Darck theme---*/

html.dark {
    .main-slider__title:before {
        background-image: url("../../../i/main-slider-shape-9-dark.png");
    }
  
}


/*-----Animation elements-----*/

