.icons {
    list-style-type: none;
    padding-left: 0;
    margin-top: 0;
    margin-bottom: 0;
    display: flex;
    li {
        margin-left: 10px;
        margin-right: 10px;
        display: flex;
        align-items: center;
        a {
            text-decoration: none;
            font-size: rem(14px);
            color: $white;
            -webkit-transition: all 500ms ease;
            transition: all 500ms ease;
            &:hover {
                color: $ogenix-primary;
            }
        }
    }
}