.testimonial {
    padding-top: 90px;
}
.testimonial__row {
    display: flex;
}
.testimonial__col {
    flex-basis: 50%;
}
.testimonial__bg-img {
    display: block;
    position: absolute;
    left: 0;
    bottom: -10px;
    mix-blend-mode: luminosity;
    opacity: .10;
}
.testimonial__right {
    padding-top: 0;
    margin-left: 50px;
    position: relative;
}
.testimonial__carousel {
    max-width: 450px;
}

.testimonial__arrows {
    flex-direction: column;
    margin-top: 0;
    position: absolute;
    top: 50%;
    left: -25px;
}
.testimonial__arrow {
    margin: 5px 0;
}

