%semicircle  {
    content: "";
    border-radius: 50%;
    box-shadow: 0px 10px 60px 0px rgb(0 0 0 / 7%);
    position: absolute;
    width: 180px;
    height: 180px;
    left: 50%;
    transform: translateX(-50%);
    bottom: -10px;
    z-index: -1;
    -webkit-transition: all 500ms ease;
    transition: all 500ms ease;
}

.categories {
    padding-bottom: 64px;
    &__item {
        margin-bottom: 50px;
    }
    &__box {
        height: 100%;
        display: flex;
        flex-direction: column;
        position: relative;
    }
    &__title-box {
        margin-bottom: 50px;
    }
    &__title {
        @extend %title;
        text-align: center
    }
    &__subtitle {
        @extend %subtitle;
        color: $ogenix-base;
        text-align: center;
    }
    &__img-box {
        position: relative;
        display: flex;
        justify-content: center;
        z-index: 2;
        img {
            width: 180px;
            height: 180px;
            object-fit: cover;
            border-radius: 50%;
        }
        &:before {
            @extend %semicircle;
            background-color: rgb(255, 255, 255);
           
        }
        &:after {
            @extend %semicircle;
            opacity: 0;
        }
    }
    &__text-box {
        flex-grow: 1;
        text-align: center;
        padding: 123px 45px 42px;
        margin-top: -91px;
        border-radius: 6px;
        overflow: hidden;
        z-index: 1;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
    }
    &__cat__title {
        font-size: rem(20px);
        font-weight: 900;
        line-height: rem(30px);
        margin-bottom: 4px;
    }
    &__text {
        font-size: rem(14px);
        font-weight: 500;
        line-height: rem(26px);
    }
    &__arrow-box {
        position: absolute;
        bottom: -26px;
        left: 50%;
        transform: translateX(-50%);
        transition: all 800ms ease;
        opacity: 0;
        z-index: 3;
    }
     &__box:hover &__img-box:after {
        opacity: 1;
    }
    &__box:hover  &__arrow-box {
        opacity: 1;
        transform: translateX(-50%);
        transition-delay: 200ms;
    }
    &__arrow {
        text-decoration: none;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 52px;
        height: 52px;
        border-radius: 50%;
        -webkit-transition: all 500ms ease;
        transition: all 500ms ease;
        span {
            font-size: 1rem;
            color: $white;
        }
    }
    &__box_green &__img-box:after {
        background-color: rgb(80, 167, 44);
    }
    &__box_green &__arrow {
        background-color: $ogenix-base;
    }
    &__box_red &__img-box:after {
        background-color: rgb(255, 81, 81);
    }
    &__box_red &__arrow {
        background-color: rgb(255, 81, 81);
    }
    &__box_yellow &__img-box:after {
        background-color: rgb(254, 188, 60);
    }
    &__box_yellow &__arrow {
        background-color: rgb(254, 188, 60);
    }
    &__box_brown &__img-box:after {
        background-color: rgb(170, 129, 100);
    }
    &__box_brown &__arrow {
        background-color: rgb(170, 129, 100);
    }
}

/*----Light theme---*/

html.light {
    .categories__title {
      color: $ogenix-black;
    }
    .categories__box_green .categories__text-box {
        background-image: asset(image, 'categories-one-content-shape-1.png');
    }
    .categories__box_red .categories__text-box {
        background-image: asset(image, 'categories-one-content-shape-2.png');
    }
    .categories__box_yellow .categories__text-box {
        background-image: asset(image, 'categories-one-content-shape-3.png');
    }
    .categories__box_brown .categories__text-box {
        background-image: asset(image, 'categories-one-content-shape-4.png');
    }
    .categories__cat-title {
        color: $ogenix-black;
    }
    .categories__text {
        color: $ogenix-gray;
    }
}


/*----Darck theme---*/

html.dark {
    .categories__title {
        color: $white;
    }
    .categories__box_green .categories__text-box {
        background-image: asset(image, 'categories-one-content-shape-1-dark.png');
    }
    .categories__box_red .categories__text-box {
        background-image: asset(image, 'categories-one-content-shape-2-dark.png');
    }
    .categories__box_yellow .categories__text-box {
        background-image: asset(image, 'categories-one-content-shape-3-dark.png');
    }
    .categories__box_brown .categories__text-box {
        background-image: asset(image, 'categories-one-content-shape-4-dark.png');
    }
    .categories__cat-title {
        color: $white;
    }
    .categories__text {
        color: $white;
    }
  }