.feature {
    &__inner {
        position: relative;
        display: block;
        box-shadow: 0px 10px 60px 0px rgb(0 0 0 / 7%);
        padding: 35px 15px 5px;
        border-radius: rem(6px);
        position: relative;
        margin-top: -20px;
        z-index: 2;
    }
    &__list {
        list-style-type: none;
        padding-left: 0;
        margin-top: 0;
        margin-bottom: 0;
    }
    &__single {
        display: flex;
        justify-content: center;
        align-items: start;
        margin-bottom: 30px;
    }
    &__icon {
        position: relative;
        &:before {
            content: "";
            position: absolute;
            top: 10px;
            right: -20px;
            width: 46px;
            height: 46px;
            border-radius: 50%;
            background-color: $ogenix-base;
            opacity: .10;
        }
        span {
            font-size: rem(64px);
            color: $ogenix-base;
        }
    }
    &__content {
        margin-left: 30px;
        flex-basis: 250px;
    }
    &__title {
        font-size: rem(18px);
        font-weight: 900;
        line-height: rem(26px);
        margin-top: 0;
        margin-bottom: 0;
    }
    &__text {
        line-height: rem(26px);
        margin-top: 0;
        margin-bottom: 0;
    }

}


/*----Darck theme---*/

html.dark {
    .feature__inner {
      background-color: $ogenix-black;
    }
    .feature__title {
        color: $white;
    }
    .feature__text {
        color: $white;
    }

}


/*----Light theme---*/

html.light {
    .feature__inner {
      background-color: $white;
    }
    .feature__title {
        color: $ogenix-black;
    }
    .feature__text {
        color: $ogenix-gray;
    }
  }