.product {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgb(255, 255, 255);
    text-align: center;
    border: 1px solid $ogenix-bdr-color;
    border-radius: 6px;
    -webkit-transition: all 500ms ease;
    transition: all 500ms ease;
    z-index: 1;
    padding-bottom: 30px;
    &:hover {
        box-shadow: 0px 10px 60px 0px rgb(0 0 0 / 7%);
    }
    &__img-box {
        //width: 275px;
        max-width: 100%;
        img {
            width: 100%;
            object-fit: cover;
        }
    }
    &__content {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-left: 15px;
        padding-right: 15px;
    
    }
    &__title {
        font-size: rem(20px);
        font-weight: 900;
        line-height: rem(30px);
        margin-top: 3px;
        margin-top: 0;
        margin-bottom: 0;
        a {
            text-decoration: none;
            color: $ogenix-black;
            margin-top: 0;
            margin-bottom: 0;
        }
    }
    &__text {
        color: $ogenix-gray;
        margin-top: 0;
        margin-bottom: 0;
    }
    &__price {
        font-weight: 700;
        line-height: rem(25px);
        color: $ogenix-gray;
        margin-top: 0;
        margin-bottom: 0;
    }
    &__btn {
        margin-top: 15px;
    }
    &__icon-box {
        position: absolute;
        top: 0;
        right: 0;
        padding: 10px;
        display: flex;
        flex-direction: column;
        opacity: 0;
        -webkit-transform: perspective(400px) rotateX(20deg) translateX(100%);
        -ms-transform: perspective(400px) rotateX(20deg) translateX(100%);
        transform: perspective(400px) rotateX(20deg) translateX(100%);
        -webkit-transform-origin: bottom;
        -ms-transform-origin: bottom;
        transform-origin: bottom;
        transition-delay: .1s;
        transition-timing-function: ease-in-out;
        transition-duration: .5s;
        transition-property: all;
    }
    &__icon {
        display: block;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        background-color: $ogenix-bdr-color;
        font-size: 1rem;
        text-decoration: none;
        color: $ogenix-black;
        margin-bottom: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all 200ms linear;
        transition-delay: 0.1s;
        &:hover {
            background-color: $ogenix-black;
            color: $ogenix-bdr-color;
        }
    }
    &:hover &__icon-box {
        opacity: 1.0;
        -webkit-transform: perspective(400px) rotateX(0deg) translateX(0%);
        -ms-transform: perspective(400px) rotateX(0deg) translateX(0%);
        transform: perspective(400px) rotateX(0deg) translateX(-0%);
        transition-delay: .3s;
        transition-timing-function: ease-in-out;
        transition-duration: .5s;
        transition-property: all;
    }


}

.stars {
    list-style-type: none;
    padding-left: 0;
    display: flex;
    align-items: center;
    margin-top: 0;
    margin-bottom: 0;
    li {
        color: $ogenix-primary;
    }
}