.page-header {
    position: relative;
    display: block;
    padding: 175px 0 176px;
    z-index: 2;
    overflow: hidden;
    &__bg {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: -1;
        opacity: 0.8;
    }
    &__ripped-paper {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        height: 59px;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        transform: rotate(180deg);
        z-index: 1;
    }
    &__bread-crumbs {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 auto;
        list-style-type: none;
        padding-left: 0;
        li {
            padding-right: 5px;
            margin-right: 5px;
            &::after {
                content: "/";
                color: $white;
                font-size: rem(16px);
            }
            &:last-child {
                padding-left: 0;
                margin-left: 0;
            }
            &:last-child::after {
                content: none;
            }
        }
        a, p {
            text-decoration: none;
            display: inline-block;
            margin-top: 0;
            margin-bottom: 0;
            color: $white;
            font-size: rem(16px);
            -webkit-transition: all 500ms ease;
            transition: all 500ms ease;
        }
    }
    &__title {
        margin-top: 0;
        margin-bottom: 0;
        font-size: rem(50px);
        color: $white;
        line-height: rem(55px);
        font-weight: 800;
        text-align: center;
    }
}

/*----Light theme---*/

html.light {
    .page-header__ripped-paper {
        background-image: url(../../../i/site-footer-ripped-paper.png);
    }
}

/*----Darck theme---*/

html.dark {
    .page-header__ripped-paper {
        background-image: url(../../../i/site-footer-ripped-paper-dark.png);
    }
}